utor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const dialog = container.resolve<IDialog>(IDialog);\n\n    const routerParams = router.getRouteParams<{ organizationGroupID: string }>();\n    backofficeUseCase.setBreadcrumb([\"Organization Groups\", \"Organization Group Detail\"]);\n    const orgGroup = ref<IGetOrganizaionGroupDetailsResponse>();\n    const allAppsOfOrgGroup = ref<Array<IApplicationData>>([]);\n    const totalOrgGroup = ref(true);\n    const byOrganization = ref(false);\n    const organizations = ref<Array<IOrganizationData>>();\n\n    onBeforeMount(() => {\n        getOrganizationGroupDetails();\n    })\n    function getOrganizationGroupDetails() {\n        executor.execute(async () => {\n            orgGroup.value = await backofficeUseCase.getOrganizationGroupDetails({organizationGroupId: routerParams.organizationGroupID});\n            allAppsOfOrgGroup.value = backofficeUseCase.getAllAppsForOrgGroup(orgGroup.value!);\n            setOrgIdsofApps();\n            organizations.value = orgGroup.value.organizations.map((org) => {\n                const organizationData: IOrganizationData = { ...org, showOrgInfo: false };\n                return organizationData; \n            });\n        }, { loading: true });\n    }\n    function setOrgIdsofApps() {\n        orgGroup.value?.organizationApplications.forEach(orgApp => {\n            allAppsOfOrgGroup.value.forEach(app => orgApp.applications.forEach(item => {\n                if(item.ID == app.ID) {\n                    app.organizationId = orgApp.organizationId;\n                }\n            }))\n        })\n    }\n    function openUpdateOrgGroupDialog() {\n        const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n            if(reason == \"component\") {\n                getOrganizationGroupDetails();\n            }\n        }\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/addOrUpdateOrgGroup.vue`)),\n            { closable: true, title: \"Update Organization Group\", width: \"542px\", maxHeight: \"379px\", closeCb }, {orgGroup: orgGroup.value?.organizationGroup}\n        );\n    }\n    function getAppsByOrganizationId(orgId: string) {\n        const orgApps = backofficeUseCase.getOrganizationAppsByOrganizationIdForOrgGroup(orgId, orgGroup.value!);\n        return orgApps!;\n    }\n    function getUserPerOrganization(orgId: string) {\n        return backofficeUseCase.getUsersPerOrganizationForOrgGroup(orgId, orgGroup.value!);\n    }\n</script>\n<template>\n    <div class=\"update-org-group-div\">\n        <button class=\"update-btn\" @click=\"openUpdateOrgGroupDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>UPDATE ORGANIZATION GROUP</span></button>\n    </div>  \n    <div  style=\"display: flex; margin-bottom: 32px;\">\n        <div class=\"org-group-info-type\">\n            <div class=\"org-group-info-type-name\">Name</div>\n            <div class=\"org-group-info\">{{ orgGroup?.organizationGroup.name }}</div>\n        </div>\n        <div class=\"org-group-info-type\">\n            <div class=\"org-group-info-type-name\">Description</div>\n            <div class=\"org-group-info\">{{ orgGroup?.organizationGroup.description }}</div>\n        </div>\n        <div class=\"org-group-info-type\">\n            <div class=\"org-group-info-type-name\">Total User Count</div>\n            <div class=\"org-group-info\">{{ orgGroup?.users.length }}</div>\n        </div>\n        <div class=\"org-group-info-type\">\n            <div class=\"org-group-info-type-name\">Total App Count</div>\n            <div class=\"org-group-info\">{{ allAppsOfOrgGroup.length }}</div>\n        </div>\n    </div>\n    <div class=\"org-group-org-info\">\n        <div class=\"org-group-tabs\">\n            <div  :class=\"totalOrgGroup ? 'active-category' : ''\" @click=\"totalOrgGroup = true; byOrganization = false\">Total</div>\n            <div  :class=\"byOrganization ? 'active-category' : ''\" @click=\"totalOrgGroup = false; byOrganization = true\">Organizations</div>\n        </div>    \n        <div style=\"padding: 0 24px\" v-if=\"totalOrgGroup\">\n            <UserList v-if=\"orgGroup\" :users=\"orgGroup.users\" :displayOrgNames=\"true\"></UserList>\n            <AppList v-if=\"orgGroup\" :apps=\"allAppsOfOrgGroup\" :displayOrgName=\"true\"></AppList>\n        </div>\n        <div v-if=\"byOrganization\">\n            <div v-for=\"organization in organizations\" :key=\"organization.id\" class=\"org-div\">\n                <div class=\"org-name\" :style=\"{marginBottom: organization.showOrgInfo ? '20px' : '0px', borderBottom: organization.showOrgInfo ? '1px solid black' : 'none', paddingBottom: organization.showOrgInfo ? '10px': '0'}\">\n                    <span><i class=\"mdi mdi-chart-pie\" style=\"color: #E65100; margin-right: 10px\"></i>{{ organization.name }}</span>\n                    <div @click=\"organization.showOrgInfo = !organization.showOrgInfo\" style=\"cursor: pointer; margin-left: auto\">\n                        <i v-if=\"!organization.showOrgInfo\" class=\"mdi mdi-chevron-down\"></i>\n                        <i v-else class=\"mdi mdi-chevron-up\"></i>\n                    </div>\n                </div>\n                <div v-if=\"organization.showOrgInfo\">\n                    <UserList :users=\"getUserPerOrganization(organization.id)\"></UserList>\n                    <AppList :apps=\"getAppsByOrganizationId(organization.id)\"></AppList>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n    .update-org-group-div {\n        width: 85%; \n        display: flex; \n        justify-content: end;\n        position: absolute;\n        top: 80px;\n        right: 36px;\n    }\n    .update-btn {\n        border: 1px solid #151233;\n        border-radius: 4px;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        color: #151233;\n        display: flex;\n        align-items: center;\n        font-weight: 500;\n    }\n    .org-group-info-type {\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        background: #FFFFFF;\n        margin-right: 37px;\n        padding: 24px 24px 18px;\n        display: flex;\n        flex-direction: column;\n        flex: 1;\n    }\n    .org-group-info-type:last-child {\n        margin-right: 0;\n    }\n    .org-group-info-type-name {\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        color: #757575;\n        margin-bottom: 20px;\n        min-width: 100px;\n        flex: 1;\n    }\n    .org-group-info {\n        font-weight: 500;\n        font-size: 28px;\n        line-height: 33px;\n        color: #151233; \n    }\n    .org-name {\n        font-size: 30px;\n        line-height: 32px;\n        color: #151233;\n        display: flex;\n   \n    }\n    .user-app-header {\n        font-weight: 500;\n        font-size: 24px;\n        line-height: 28px;\n        color: #151233;\n        margin-bottom: 20px;\n    }\n    .user-app-list  {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    .user-app-list  tr:hover:not(:last-child):not(:first-child) {\n        background-color: #EEEEEE;\n    }\n    .user-app-list  th\n        {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 18px 36px\n    }\n    .user-app-list  td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 20px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .user-app-list  .thead {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .org-group-tabs {\n        display: flex;\n        align-items: center;\n        width: 100%;\n        margin-bottom: 20px;\n        padding: 24px 32px;\n    }\n    .org-group-tabs div {\n        color: #A1A1A1;\n        font-weight: 500;\n        font-size: 14px;\n        width: 111px;\n        padding-bottom: 8px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        cursor: pointer;\n        border: 2px solid transparent;\n    }\n    .active-category {\n        color: #151233 !important; \n        border-bottom: 2px solid !important\n    }\n    .org-group-org-info {\n        background: #FFFFFF;\n        border-radius: 8px;\n    }\n    .org-div {\n        border-bottom: 1px solid #0000002F;\n        padding: 24px;\n    }\n    .org-div:last-child {\n        border-bottom: none;\n    }\n</style>"],"sourceRoot":""}]);
